<template>
  <div class="box">
    <div class="con">
      <h3>登录</h3>
      <el-input v-model="form.username" class="in" placeholder="请输入用户名"></el-input>
      <el-input v-model="form.password" class="in" placeholder="请输入密码" show-password></el-input>
      <div class="btn">
        <el-button type="primary" round @click="login">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import {requestLogin} from  '../../util/request';
import {mapActions} from 'vuex';
import {successAlert, warningAlert } from '../../util/alert';
export default {
  data(){
    return {
      form:{
        username:'',
        password:'',
      }
    }
  },
  methods:{
    ...mapActions({
      changeUser:"requestLoginActions"
    }),
    login(){
      // 发起登录请求
      // 方式一:存入仓库和sessionStorage中
      // 在这块先发起登录请求在修改仓库的数据
      requestLogin(this.form).then(res=>{
        if(res.data.code == 200){
          successAlert('登陆成功');
          // 将数据存入仓库中
          this.changeUser(res.data.list);
          // 跳转页面
          this.$router.push('/');
        }
      })





      // requestLogin(this.form).then(res=>{
      //   if(res.data.code == 200){
      //     successAlert('登录成功');
          // 方式二:
          // if(localStorage.getItem('data') !== null){
          //   localStorage.removeItem('data');
          // }
          //   localStorage.setItem('data',JSON.stringify(res.data.list))

          // this.$router.push("/");
        // }else{
        //   warningAlert(res.data.msg);
        // }
      // })
    }
  }
}
</script>

<style scoped>
.box{
  width:100vw;
  height: 100vh;
  background:linear-gradient(#0f0f0f,#988665);
}
.con{
  width:400px;
  height: 200px;
  background:white;
  padding:20px;
  position:fixed;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  border-radius: 10px;
}
h3{
  text-align: center;
  line-break: 30px;
  font-size:30px;
  margin-bottom:10px;
}
.btn{
  text-align: center;
}
.in{
  margin-bottom:20px;
}
</style>



